import { Component, Input, Output, ElementRef, EventEmitter, OnInit, OnChanges, SimpleChanges } from '@angular/core';

declare var EasyPieChart: any;

@Component({
    selector: 'easy-pie-chart',
    template: `<div>Loading...</div>`
})
export class EasyPieChartComponent implements OnInit, OnChanges {
    @Input() percent: any;
    @Input() options: any;

    public element: any;

    private pieChart: any;

    constructor(el: ElementRef) {
        this.element = el.nativeElement;

        let options = {
            barColor: '#ef1e25',
            trackColor: '#f9f9f9',
            scaleColor: '#dfe0e0',
            scaleLength: 5,
            lineCap: 'round',
            lineWidth: 3,
            size: 110,
            rotate: 0,
            animate: {
                duration: 1000,
                enabled: true
            }
        };
        this.options = Object.assign(options, this.options);
    }

    public ngOnInit(): void {
        this.element.innerHTML = "";
        this.pieChart = new EasyPieChart(this.element, this.options);
        this.pieChart.update(this.percent);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (!changes['percent'].isFirstChange()) {
            this.pieChart.update(this.percent);
        }
    }
}



